/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

::placeholder {
  color: rgba(52, 63, 116, 0.7019607843);
  font-size: 2rem;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Satoshi", sans-serif;
  font-size: 1.6rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

i {
  color: #7684BC;
}

h1,
h2,
h3 {
  font-family: "Satoshi", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 8rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
}

.title_page {
  text-align: center;
  margin: 5rem 0;
  font-weight: 900;
  color: #6280CF;
}

.description_page {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor {
    display: grid;
    grid-template-columns: 0.5fr 2fr;
    column-gap: 2rem;
    min-height: 100vh;
    padding: 1rem;
  }
  .contenedor .sidebar {
    background-color: #343F74;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 4rem 1rem;
    position: relative;
  }
  .contenedor .sidebar .sidebar_image {
    max-width: 100%;
  }
  .contenedor .sidebar .sidebar_image img {
    border-radius: 50%;
  }
  .contenedor .sidebar .toggle {
    position: absolute;
    top: 5%;
    right: -1.5rem;
    font-size: 3rem;
    transform: rotate(180deg);
    background: #4E66C2;
    border-radius: 50%;
    color: #F2F5FC;
  }
  .contenedor .sidebar .menu_links {
    width: 100%;
    margin: 0;
    padding: 0 2rem;
    list-style-type: none;
  }
  .contenedor .sidebar .menu_item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 5rem;
    margin-top: 1.5rem;
    padding: 0 2rem;
    border-radius: 1.5rem;
    background-color: #7684BC;
    list-style: none;
  }
  .contenedor .sidebar .menu_item.active {
    background-color: #F2F5FC;
  }
  .contenedor .sidebar .menu_item.active .menu_link,
  .contenedor .sidebar .menu_item.active .menu_icon {
    color: #4E66C2;
  }
  .contenedor .sidebar .menu_item .menu_link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    font-weight: 700;
    color: #F2F5FC;
  }
  .contenedor .sidebar .menu_item .menu_icon {
    color: #F2F5FC;
  }
  .contenedor .sidebar_footer {
    width: 100%;
  }
  .contenedor .sidebar_footer .user {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 2rem;
  }
  .contenedor .sidebar_footer .user .user_avatar {
    border-radius: 50%;
    width: 5rem;
    height: 5rem;
  }
  .contenedor .sidebar_footer .user .user_name {
    color: #F2F5FC;
  }
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .login_image {
  height: 20rem;
  background-image: url(../img/LoginImage.jpeg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .login_image {
    height: auto;
  }
}
.contenedor-app .login_session {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

.formulario {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.campo {
  width: 100%;
  display: flex;
  margin-bottom: 3rem;
  box-shadow: 1px 4px 20px rgba(52, 63, 116, 0.4);
  border-radius: 1.5rem;
  padding: 1.5rem 2rem;
  background: #F2F5FC;
}
.campo input {
  border-radius: 1.5rem;
  background: #F2F5FC;
  color: #4E66C2;
  letter-spacing: 0.1rem;
  width: 100%;
  border: none;
}
.campo input:focus {
  outline: none;
}

.boton {
  background-color: #7684BC;
  padding: 1.5rem 4rem;
  color: #F2F5FC;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #5465ab;
  cursor: pointer;
}